.shopping-wrapper {
  @media screen and (min-width: $desktop) {
    display: flex;
    gap: 40px;
  }
  padding-left: 20px;
  padding-right: 20px;
  // justify-content: center;
}
.shopping-cart {
  width: 100%;
}
.shopping-wrapper .support__section {
  margin-top: 40px;
  @media screen and (max-width: $desktop) {
    display: none;
  }
}

.shopping-cart__title {
  font-weight: 700;
  font-size: 32px;
  line-height: 1.08;
  letter-spacing: -0.04em;
  color: var(--color-shop-list-text);
  margin-top: 40px;
  margin-bottom: 40px;
  @media screen and (min-width: 768px) {
    font-size: 48px;
  }
}
.shopping-cart__title--color-text {
  color: var(--color-blue);
}
.shopping-cart__list {
  display: flex;
  gap: 20px;
  flex-direction: column;
}
.shopping-cart__item {
  // width: 100%;
  margin-left: auto;
  margin-right: auto;
  position: relative;
  display: grid;
  align-items: start;
  margin-right: auto;
  margin-left: auto;
  padding: 30px 24px;
  background: var(--color-shop-card-background);
  border: 2px solid rgba(79, 46, 232, 0.4);
  border-radius: 16px;
  @media screen and (max-width: 767px) {
    grid-column-gap: 20px;
    grid-template-columns: 98px 1fr;
    grid-auto-rows: minmax(0, auto);
    grid-auto-columns: minmax(0, auto);
    max-width: 335px;
  }

  @media screen and (min-width: 768px) {
    width: 704px;
    grid-column-gap: 24px;
    grid-template-columns: 116px 1fr;
    grid-auto-rows: minmax(0, auto);
    grid-auto-columns: minmax(0, auto);
  }

  @media screen and (min-width: 1280px) {
    // width: 996px;
    width: 100%;
  }
}
.shopping-cart__book-img {
  width: 98px;
  height: 139px;
  border-radius: 8px;

  @media screen and (max-width: 768px) {
    grid-row: 1/3;
    grid-column: 1/2;
    margin-bottom: 4px;
  }

  @media screen and (min-width: 767px) {
    grid-row: 1/3;
    grid-column: 1/2;
    width: 116px;
    height: 165px;
  }
}
.shopping-cart__main-info {
  //   width: 14px;

  @media screen and (max-width: 768px) {
    margin-bottom: 24px;
    align-self: start;
  }
  @media screen and (min-width: $tablet) {
    margin-bottom: 14px;
  }
}

.shopping-cart__book-title {
  font-weight: 700;
  font-size: 16px;
  line-height: 1.12;
  letter-spacing: -0.04em;
  text-transform: uppercase;
  margin-bottom: 4px;
  color: var(--color-shop-list-text);
}

.shopping-cart__book-category {
  font-size: 12px;
  line-height: 1.17;
  max-width: 150px;
  letter-spacing: 0.03em;

  font-size: 12px;
  line-height: 1.17;
  letter-spacing: 0.03em;
  color: var(--color-grey-light);
}

.shopping-cart__btn-delete {
  position: absolute;
  top: 28px;
  right: 14px;
  padding: 0;
  margin: 0;
  border: none;
  //   text-align: center;
  //   overflow: hidden;
  //   display: flex;
  //   align-items: center;
  //   justify-content: center;
  width: 28px;
  height: 28px;
  border-radius: 50%;
  margin-left: auto;
  cursor: pointer;
  @include transition(transform);
  &:hover,
  &:focus {
    transform: scale(1.1);
  }
  color: var(--color-close-shop-card);
}
.delete-icon {
  width: 28px;
  height: 28px;
  fill: currentColor;
}

.shopping-cart__book-description {
  @media screen and (max-width: $tablet) {
    grid-column: 1/3;
    grid-row: 5/5;
  }
  @media screen and (min-width: $tablet) {
    grid-column: 2/3;
    grid-row: 2/2;
  }
  font-size: 14px;
  line-height: 1.29;
  letter-spacing: -0.04em;
  color: var(--color-shop-list-text);
}

.shopping-cart__book-author {
  font-size: 12px;
  line-height: 1.17;
  @media screen and (max-width: $tablet) {
    grid-row: 4/4;
  }
  @media screen and (min-width: $tablet) {
    grid-row: 4/4;
    grid-column: 2/2;
  }
  letter-spacing: -0.04em;
  color: var(--color-grey-light);
  margin-bottom: 20px;
}

.shopping-cart__links {
  display: flex;
  align-items: center;
  gap: 14px;
  @media screen and (max-width: $tablet) {
    grid-row: 2/2;
    grid-column: 2/2;
    margin-top: -45px;
  }
  @media screen and (min-width: $tablet) {
    grid-column: 3/3;
    grid-row: 4/4;
  }
}

.shopping-cart__book-shop {
  @include transition(transform, filter);
  mix-blend-mode: luminosity;
  &:hover,
  &:focus {
    transform: scale(1.1);
    filter: drop-shadow(0px 3px 3px var(--color-drop-shadow-shopping-cart));
  }
}

.theme-dark .amazon-link {
  filter: invert(1);
}

.shopping-cart__warning {
  margin-top: 120px;
  text-align: center;
  max-width: 265px;
  margin-right: auto;
  margin-left: auto;
}

.shopping-cart__warning-text {
  font-size: 14px;
  line-height: 1.29;
  letter-spacing: -0.02em;
  // цвет добавить в переменную
  color: rgba(0, 0, 0, 0.6);
  margin-bottom: 14px;
  max-width: 244px;
}

.shopping-cart__warning-img {
  margin-left: auto;
  margin-right: auto;
}
